const { Component, Mixin } = Shopware;
import template from './sw-cms-block-dreisc-dynamic-four-column.html.twig';
import './sw-cms-block-dreisc-dynamic-four-column.scss';
import './../../../dreisc-cms/mixin/cms-block-entity-manager'
import './../../../dreisc-cms/mixin/cms-parent-finder'

Component.extend('sw-cms-block-dreisc-dynamic-four-column', 'sw-cms-block-dreisc-dynamic-three-column', {
    template,

    data() {
        return {
            cmsBlockAdditionalConfig: {
                dreiscCmsFirstColumnWidth: true,
                dreiscCmsSecondColumnWidth: true,
                dreiscCmsThirdColumnWidth: true,
                dreiscCmsColumnBreakPixel: true
            },

            subSectionNames: [ 'firstColumn', 'secondColumn', 'thirdColumn', 'fourthColumn' ],

            columnWidthDefault: 25,
            dreiscCmsColumnBreakPixelDefault: 100
        }
    },

    created() {
        /** Active the block config fields */
        this.$parent.block.cmsBlockAdditionalConfig = this.cmsBlockAdditionalConfig;

        /** Set the availables sub sections for the navigation tree */
        this.$parent.block.subSectionNames = this.subSectionNames;
    },

    computed: {
        widthsForCalculation() {
            return [
                this.dreiscCmsFirstColumnWidth,
                this.dreiscCmsSecondColumnWidth,
                this.dreiscCmsThirdColumnWidth
            ]
        },

        styleFourthColumn() {
            return 'flex: 1 1 ' + this.dreiscCmsColumnBreakPixel + 'px; min-width: ' + this.columnWidths[3] + '%;';
        },

        dreiscCmsThirdColumnWidth: {
            get() {
                if (this.block.name) {
                    /** Cache breaker */
                }

                if ('object' !== typeof this.block.customFields || null === this.block.customFields) {
                    this.block.customFields = {};
                }

                if ('undefined' === typeof this.block.customFields.dreisc_cms_third_column_width) {
                    this.block.customFields.dreisc_cms_third_column_width = this.columnWidthDefault;
                }

                this.block.customFields.dreisc_cms_third_column_width = parseFloat(this.block.customFields.dreisc_cms_third_column_width);
                if (this.block.customFields.dreisc_cms_third_column_width <= 0) {
                    this.block.customFields.dreisc_cms_third_column_width = this.columnWidthDefault;
                }

                return parseFloat(this.block.customFields.dreisc_cms_third_column_width);
            }
        }
    }
});
