const { Component, Mixin } = Shopware;
import template from './sw-cms-block-dynamic-two-column.html.twig';
import './sw-cms-block-dynamic-two-column.scss';
import './../../../dreisc-cms/mixin/cms-block-entity-manager'
import './../../../dreisc-cms/mixin/cms-parent-finder'

Component.extend('sw-cms-block-dynamic-two-column', 'cms-block-group', {
    template,

    data() {
        return {
            cmsBlockAdditionalConfig: {
                dreiscCmsFirstColumnWidth: true,
                dreiscCmsColumnBreakPixel: true
            },

            subSectionNames: [ 'firstColumn', 'secondColumn' ],

            columnWidthDefault: 50,
            dreiscCmsColumnBreakPixelDefault: 150
        }
    },

    created() {
        /** Active the block config fields */
        this.$parent.block.cmsBlockAdditionalConfig = this.cmsBlockAdditionalConfig;

        /** Set the availables sub sections for the navigation tree */
        this.$parent.block.subSectionNames = this.subSectionNames;
    },

    computed: {
        widthsForCalculation() {
            return [
                this.dreiscCmsFirstColumnWidth
            ]
        },

        columnWidths() {
            const calculatedWidths = [];
            let lastIndex = null;
            const widths = this.widthsForCalculation;

            let currentWidthOffset = 0;
            widths.forEach((width, index) => {
                lastIndex = index;
                currentWidthOffset = currentWidthOffset + width;

                /** Is ready to close? */
                if (!widths[index+1]) {
                    /** Is ready, because there is no next width */
                } else if (currentWidthOffset + widths[index+1] > 100) {
                    /** Is ready, because the current and the next width would be over 100% */
                    /** We add the difference to 100% if necessary */
                    width = width + (100 - currentWidthOffset);

                    /** Reset the currentWidthOffset */
                    currentWidthOffset = 0;
                }

                calculatedWidths[index] = width;
            });

            /** We also set the last item */
            calculatedWidths[lastIndex+1] = 100 - currentWidthOffset;
            if (0 === calculatedWidths[lastIndex+1]) {
                calculatedWidths[lastIndex+1] = 100;
            }

            return calculatedWidths;
        },

        styleFirstColumn() {
            return 'flex: 1 1 ' + this.dreiscCmsColumnBreakPixel + 'px; min-width: ' + this.columnWidths[0] + '%;';
        },

        styleSecondColumn() {
            return 'flex: 1 1 ' + this.dreiscCmsColumnBreakPixel + 'px; min-width: ' + this.columnWidths[1] + '%;';
        },

        dreiscCmsFirstColumnWidth: {
            get() {
                if (this.block.name) {
                    /** Cache breaker */
                }

                if ('object' !== typeof this.block.customFields || null === this.block.customFields) {
                    this.block.customFields = {};
                }

                if ('undefined' === typeof this.block.customFields.dreisc_cms_first_column_width) {
                    this.block.customFields.dreisc_cms_first_column_width = this.columnWidthDefault;
                }

                this.block.customFields.dreisc_cms_first_column_width = parseFloat(this.block.customFields.dreisc_cms_first_column_width);
                if (this.block.customFields.dreisc_cms_first_column_width <= 0) {
                    this.block.customFields.dreisc_cms_first_column_width = this.columnWidthDefault;
                }

                return parseFloat(this.block.customFields.dreisc_cms_first_column_width);
            }
        },

        dreiscCmsColumnBreakPixel: {
            get() {
                if (this.block.name) {
                    /** Cache breaker */
                }

                if ('object' !== typeof this.block.customFields || null === this.block.customFields) {
                    this.block.customFields = {};
                }

                if ('undefined' === typeof this.block.customFields.dreisc_cms_column_break_pixel) {
                    this.block.customFields.dreisc_cms_column_break_pixel = this.dreiscCmsColumnBreakPixelDefault;
                }

                this.block.customFields.dreisc_cms_column_break_pixel = parseInt(this.block.customFields.dreisc_cms_column_break_pixel);
                if (this.block.customFields.dreisc_cms_first_column_width <= 0) {
                    this.block.customFields.dreisc_cms_first_column_width = this.dreiscCmsColumnBreakPixelDefault;
                }

                return parseInt(this.block.customFields.dreisc_cms_column_break_pixel);
            }
        }
    },

    watch: {
        block: {
            deep: true,
            handler() {
            }
        }
    }
});
