import template from './sw-cms-el-dreisc-cms-alert.html.twig';
import './sw-cms-el-dreisc-cms-alert.scss';

const { Component, Mixin } = Shopware;

Shopware.Component.register('sw-cms-el-dreisc-cms-alert', {
    template,

    mixins: [
        Mixin.getByName('cms-element')
    ],

    computed: {
        alertType() {
            return this.element.config.alertType.value ? this.element.config.alertType.value : 'primary';
        },

        hasIcon() {
            return !this.element.config.disableIcon.value;
        },

        alertIcon() {
            switch (this.alertType) {
                case 'danger':
                    return 'default-badge-blocked';
                case 'success':
                    return 'default-basic-checkmark-circle';
                case 'warning':
                    return 'default-badge-warning';
            }

            return 'default-badge-alert';
        },

        alertClasses() {
            const classes = [];

            classes.push('alert');
            classes.push('alert-' + this.alertType);

            if (this.hasIcon) {
                classes.push('alert-has-icon');
            }

            return classes.join(' ');
        }
    },

    created() {
        this.createdComponent();
    },

    methods: {
        createdComponent() {
            this.initElementConfig('dreisc-cms-alert');
        },

        onBlur(content) {
            this.emitChanges(content);
        },

        onInput(content) {
            this.emitChanges(content);
        },

        emitChanges(content) {
            if (content !== this.element.config.content.value) {
                this.element.config.content.value = content;
                this.$emit('element-update', this.element);
            }
        }
    }
});
