const { Component, Mixin } = Shopware;
import template from './sw-cms-block-tabs.html.twig';
import './sw-cms-block-tabs.scss';
import './../../../dreisc-cms/mixin/cms-block-entity-manager'
import './../../../dreisc-cms/mixin/cms-parent-finder'

Component.extend('sw-cms-block-tabs', 'cms-block-group', {
    template,

    inject: [
        'repositoryFactory'
    ],

    data() {
        return {
            menuPosition: 'left-offset',
            activeTabIndex: 0,

            initialTabLabel: 'Tab 1'
        }
    },

    created() {
        /** Active the block config fields */
        this.$parent.block.cmsBlockAdditionalConfig = {
            cmsBlockDynamicSlotItemsManagement: true
        };
    },

    computed: {

        slotRepository() {
            return this.repositoryFactory.create('cms_slot');
        },

        tabs() {
            const tabs = [];

            if(0 === this.$parent.block.slots.length) {
                this.addInitialTab();
            }

            this.$parent.block.slots.forEach(item => {
                let position = 0;

                if (item.customFields && item.customFields.cms_panel_position) {
                    position = item.customFields.cms_panel_position
                } else if (item.translated && item.translated.customFields && item.translated.customFields.cms_panel_position) {
                    position = item.translated.customFields.cms_panel_position
                }

                tabs.push({
                    key: item.slot,
                    position: position,
                    headline: item.config.content.value
                });
            });

            /** Sortieren */
            tabs.sort((tabA, tabB) => {
                return tabA.position - tabB.position;
            });

            /** Set the available sub sections for the navigation tree */
            const tabKeys = [];
            const subSectionNamesAlias = [];
            tabs.forEach(tab => {
                tabKeys.push(tab.key);
                subSectionNamesAlias[tab.key] = tab.headline;
            });

            this.$parent.block.subSectionNames = tabKeys;
            this.$parent.block.subSectionNamesAlias = subSectionNamesAlias;

            return tabs;

        },

        styleFirstColumn() {
            return 'flex: 1 1 ' + this.dreiscCmsColumnBreakPixel + 'px; min-width: ' + this.dreiscCmsFirstColumnWidth + '%;';
        },

        styleSecondColumn() {
            let frValue = 100 - this.dreiscCmsFirstColumnWidth;
            if (frValue < 0) {
                frValue = 50;
            }

            return 'flex: 1 1 ' + this.dreiscCmsColumnBreakPixel + 'px; min-width: ' + frValue + '%;';
        }
    },

    methods: {
        addInitialTab() {
            const element = this.slotRepository.create();
            element.blockId = this.$parent.block.id;
            element.slot = 'panel0';
            element.type = 'text';
            element.data = {};
            element.config = {
                content: {
                    source: 'static',
                    value: this.initialTabLabel
                }
            };
            element.customFields = {
                cms_panel_position: 1
            }

            this.$parent.block.slots.push(element);
        }
    },

    watch: {
        block: {
            deep: true,
            handler() {
            }
        }
    }
});
