import template from './sw-cms-el-config-dreisc-cms-alert.html.twig';
import './sw-cms-el-config-dreisc-cms-alert.scss';
const { Component, Mixin } = Shopware;

Shopware.Component.register('sw-cms-el-config-dreisc-cms-alert', {
    template,

    data() {
        return {
            selectedAlertType: null,

            alertTypes: [
                { key: 'primary', name: this.$tc('swCmsBlock.elements.dreiscCmsAlert.configFields.alertTypes.list.primary') },
                { key: 'secondary', name: this.$tc('swCmsBlock.elements.dreiscCmsAlert.configFields.alertTypes.list.secondary') },
                { key: 'danger', name: this.$tc('swCmsBlock.elements.dreiscCmsAlert.configFields.alertTypes.list.danger') },
                { key: 'success', name: this.$tc('swCmsBlock.elements.dreiscCmsAlert.configFields.alertTypes.list.success') },
                { key: 'warning', name: this.$tc('swCmsBlock.elements.dreiscCmsAlert.configFields.alertTypes.list.warning') },
                { key: 'info', name: this.$tc('swCmsBlock.elements.dreiscCmsAlert.configFields.alertTypes.list.info') },
                { key: 'dark', name: this.$tc('swCmsBlock.elements.dreiscCmsAlert.configFields.alertTypes.list.dark') }
            ]
        }
    },

    mixins: [
        Mixin.getByName('cms-element')
    ],

    created() {
        this.createdComponent();
    },

    methods: {
        createdComponent() {
            this.initElementConfig('dreisc-cms-alert');
        },

        onBlur(content) {
            this.emitChanges(content);
        },

        onInput(content) {
            this.emitChanges(content);
        },

        emitChanges(content) {
            if (content !== this.element.config.content.value) {
                this.element.config.content.value = content;
                this.$emit('element-update', this.element);
            }
        }
    }
});
