import template from './sw-cms-el-config-dreisc-cms-google-maps.html.twig';
import './sw-cms-el-config-dreisc-cms-google-maps.scss';
const { Component, Mixin } = Shopware;

Shopware.Component.register('sw-cms-el-config-dreisc-cms-google-maps', {
    template,

    data() {
        return {
            pluginConfigRoute: {
                name: 'sw.plugin.settings',
                params: {
                    namespace: 'CmsPro'
                }
            }
        }
    },

    mixins: [
        Mixin.getByName('cms-element')
    ],

    created() {
        this.createdComponent();
    },

    methods: {
        createdComponent() {
            this.initElementConfig('dreisc-cms-google-maps');
        },

        onBlur(content) {
            this.emitChanges(content);
        },

        onInput(content) {
            this.emitChanges(content);
        },

        emitChanges(content) {
            if (content !== this.element.config.infoDialogText.value) {
                this.element.config.infoDialogText.value = content;
                this.$emit('element-update', this.element);
            }
        }
    }
});
