import template from './sw-cms-el-dreisc-cms-google-maps.html.twig';
import './sw-cms-el-dreisc-cms-google-maps.scss';

const { Component, Mixin } = Shopware;

Shopware.Component.register('sw-cms-el-dreisc-cms-google-maps', {
    template,

    inject: [
        'dreiscCmsPluginConfigApiService'
    ],

    mixins: [
        Mixin.getByName('cms-element')
    ],

    computed: {
        coordinateLat() {
            return this.element.config.coordinateLat.value;
        },

        coordinateLng() {
            return this.element.config.coordinateLng.value;
        },

        minHeight() {
            return parseInt(this.element.config.minHeight.value) + 'px';
        },

        markPosition() {
            return this.element.config.markPosition.value;
        },

        showInfoDialog() {
            return this.element.config.showInfoDialog.value
        },

        infoDialogText() {
            return this.element.config.infoDialogText.value
        },

        zoom() {
            return parseInt(this.element.config.zoom.value);
        },

        googleMapsDivId() {
            return 'dreisc-cms-google-maps-' + this._uid;
        }
    },

    created() {
        this.createdComponent();
    },

    methods: {
        createdComponent() {
            this.initGoogleApi();
            this.initElementConfig('dreisc-cms-google-maps');
        },

        initGoogleApi() {
            const me = this;

            /** Abort, if google is already init */
            if('undefined' !== typeof google || window.googleApiLoadLocked === true) {
                return;
            }

            /** Lock for multi load */
            window.googleApiLoadLocked = true;

            this.dreiscCmsPluginConfigApiService.fetchPluginConfig().then(pluginConfig => {
                if (!pluginConfig || !pluginConfig.googleApiKey || '' === pluginConfig.googleApiKey.trim()) {
                    window.googleApiLoadLocked = false;
                    return;
                }

                // Create the script tag, set the appropriate attributes
                var script = document.createElement('script');
                script.src = 'https://maps.googleapis.com/maps/api/js?key=' + pluginConfig.googleApiKey + '&callback=initMap';
                script.defer = true;

                // Attach your callback function to the `window` object
                window.initMap = function() {
                    window.googleApiStatus = 'loaded';
                };

                // Append the 'script' element to 'head'
                document.head.appendChild(script);
            });
        },

        createMapWaitForApi() {
            if ('loaded' === window.googleApiStatus) {
                this.createMap();
            } else {
                setTimeout(() => {
                    this.createMapWaitForApi();
                }, 200);
            }
        },

        createMap() {
            /** Check coordinates */
            if (!this.coordinateLat || !this.coordinateLng) {
                console.warn('Missing param: coordinateLat or coordinateLng');
                return;
            }

            /** Format coordinates */
            const coordinateLat = parseFloat(this.coordinateLat.trim());
            const coordinateLng = parseFloat(this.coordinateLng.trim());

            const markerPosition = new google.maps.LatLng(coordinateLat, coordinateLng);
            const mapOptions = {
                zoom: this.zoom,
                center: markerPosition
            };

            const map = new google.maps.Map(document.getElementById(this.googleMapsDivId), mapOptions);

            if(this.markPosition) {
                const marker = new google.maps.Marker({
                    position: markerPosition,
                    map: map
                });

                if (this.showInfoDialog) {
                    const infoWindow = new google.maps.InfoWindow({
                        content: this.infoDialogText
                    });

                    infoWindow.open(map, marker);
                }
            }
        }
    },

    watch: {
        'element.config': {
            deep: true,
            handler() {
                this.createMapWaitForApi();
            }
        }
    }
});
