import template from './sw-cms-el-dreisc-cms-category-teaser.html.twig';
import './sw-cms-el-dreisc-cms-category-teaser.scss';

const { Component, Mixin } = Shopware;
const { Criteria } = Shopware.Data;

Shopware.Component.register('sw-cms-el-dreisc-cms-category-teaser', {
    template,

    mixins: [
        Mixin.getByName('cms-element')
    ],

    inject: [
        'repositoryFactory'
    ],

    data() {
        return {
            isHover: false,
            categoryName: null
        }
    },

    computed: {
        categoryRepository() {
            return this.repositoryFactory.create('category');
        },

        teaserTextStyles() {
            return {
                'color': this.element.config.textColor.value !== '' ? this.element.config.textColor.value : '#ffffff'
            };
        },

        teaserBackgroundStyles() {
            let backgroundColor = this.element.config.backgroundColor.value !== '' ? this.element.config.backgroundColor.value : '#4a545b';

            return {
                'background-color': backgroundColor,
                'opacity': this.element.config.opacity.value !== '' ? this.element.config.opacity.value : 0.8,
            };
        },

        displayModeClass() {
            if (!this.element.config.displayMode || this.element.config.displayMode.value === 'standard') {
                return null;
            }

            return `is--${this.element.config.displayMode.value}`;
        },

        styles() {
            return {
                'min-height': this.element.config.displayMode.value === 'cover' &&
                this.element.config.minHeight.value !== '' && this.element.config.minHeight.value !== 0 ? this.element.config.minHeight.value : '340px',
                'align-self': !this.element.config.verticalAlign.value ? null : this.element.config.verticalAlign.value
            };
        },

        wrapperStyles() {
            return {
                'display': this.element.config.displayMode.value === 'cover' ? 'block' : 'table'
            };
        },

        mediaUrl() {
            const context = Shopware.Context.api;
            const elemData = this.element.data.media;
            const mediaSource = this.element.config.media.source;

            if (mediaSource === 'mapped') {
                const demoMedia = this.getDemoValue(this.element.config.media.value);

                if (demoMedia && demoMedia.url) {
                    return demoMedia.url;
                }
            }

            if (elemData && elemData.id) {
                return this.element.data.media.url;
            }

            if (elemData && elemData.url) {
                return `${context.assetsPath}${elemData.url}`;
            }

            return `${context.assetsPath}/administration/static/img/cms/preview_mountain_large.jpg`;
        }
    },

    watch: {
        cmsPageState: {
            deep: true,
            handler() {
                this.$forceUpdate();
            }
        },
        'element.config.categoryId.value': {
            handler() {
                this.updateCategoryName();
            }
        },
        'element.config.differentTeaserText.value': {
            handler() {
                this.updateCategoryName();
            }
        }
    },

    created() {
        this.createdComponent();
    },

    methods: {
        createdComponent() {
            this.initElementConfig('dreisc-cms-category-teaser');
            this.initElementData('dreisc-cms-category-teaser');

            this.updateCategoryName();
        },

        updateCategoryName() {
            if (this.element.config.differentTeaserText && this.element.config.differentTeaserText.value !== '') {
                this.categoryName = this.element.config.differentTeaserText.value;
            } else if (this.element.config.categoryId && this.element.config.categoryId.value) {
                const categoryId = this.element.config.categoryId.value;

                this.categoryRepository.get(categoryId, Shopware.Context.api).then((category) => {
                    this.categoryName = category.name;
                });
            } else {
                this.categoryName = this.$tc('swCmsBlock.elements.dreiscCmsCategoryTeaser.component.categoryEmptyText')
            }
        }
    }
});
